import React, { useState } from 'react';
import { 
  Settings, 
  FileText, 
  Code, 
  Package, 
  Grid,
  ShoppingCart, 
  Users, 
  BarChart3, 
  Monitor,
  Layout,
  Video,
  Gift,
  Star,
  BookOpen,
  ChevronDown,
  ChevronRight,
  Edit,
  List,
  HelpCircle,
  Search
} from 'lucide-react';

const Sidebar = ({ activeTab, setActiveTab, isOpen, setIsOpen }) => {
  const [expandedMenus, setExpandedMenus] = useState({});
  
  const menuItems = [
    { id: 'dashboard', label: 'Dashboard', icon: BarChart3 },
    { id: 'basic-info', label: 'Basic Information', icon: Settings },
    { id: 'site', label: 'Site', icon: Monitor },
    { id: 'footer', label: 'Footer', icon: Layout },
    { id: 'policies', label: 'Policies', icon: FileText },
    { id: 'api', label: 'API', icon: Code },
    { id: 'products', label: 'Products', icon: Package },
    { id: 'categories', label: 'Categories', icon: Grid },
    { 
      id: 'blog', 
      label: 'Blog', 
      icon: BookOpen, 
      hasDropdown: true,
      children: [
        { id: 'blog-posts', label: 'Posts', icon: Edit },
        { id: 'blog-categories', label: 'Categories', icon: List },
        { id: 'blog-faqs', label: 'FAQs', icon: HelpCircle },
        { id: 'blog-seo', label: 'SEO', icon: Search }
      ]
    },
    { id: 'orders', label: 'Orders', icon: ShoppingCart },
    { id: 'customers', label: 'Customers', icon: Users },
    { id: 'ugc-videos', label: 'UGC Videos', icon: Video },
    { id: 'offer-management', label: 'Offer Management', icon: Gift },
    { id: 'review-management', label: 'Review Management', icon: Star }
  ];

  const toggleMenu = (menuId) => {
    setExpandedMenus(prev => ({
      ...prev,
      [menuId]: !prev[menuId]
    }));
  };

  return (
    <>
      {/* Mobile Overlay */}
      {isOpen && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
          onClick={() => setIsOpen(false)}
        />
      )}

      {/* Sidebar */}
      <div className={`
        fixed top-0 left-0 h-full w-64 bg-white shadow-lg z-50 transform transition-transform duration-300 ease-in-out
        ${isOpen ? 'translate-x-0' : '-translate-x-full'}
        lg:translate-x-0 lg:static lg:shadow-none
      `}>
        <div className="flex flex-col h-full">
          {/* Logo */}
          <div className="flex items-center justify-between p-6 border-b border-gray-200">
            <h1 className="text-xl font-bold text-gray-900">Admin Panel</h1>
            <button
              onClick={() => setIsOpen(false)}
              className="lg:hidden p-2 rounded-md hover:bg-gray-100"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>

          {/* Navigation */}
          <nav className="flex-1 p-4 space-y-2 overflow-y-auto scrollbar-thin scrollbar-thumb-gray-300 scrollbar-track-gray-100 hover:scrollbar-thumb-gray-400">
            {menuItems.map((item) => {
              const Icon = item.icon;
              const isExpanded = expandedMenus[item.id];
              const isActive = activeTab === item.id || (item.children && item.children.some(child => activeTab === child.id));
              
              if (item.hasDropdown) {
                return (
                  <div key={item.id} className="space-y-1">
                    <button
                      onClick={() => toggleMenu(item.id)}
                      className={`
                        w-full flex items-center justify-between px-4 py-3 text-left rounded-lg transition-colors
                        ${isActive 
                          ? 'bg-blue-50 text-blue-700 border-r-2 border-blue-700' 
                          : 'text-gray-700 hover:bg-gray-100'
                        }
                      `}
                    >
                      <div className="flex items-center">
                        <Icon className="w-5 h-5 mr-3" />
                        <span className="font-medium">{item.label}</span>
                      </div>
                      {isExpanded ? (
                        <ChevronDown className="w-4 h-4" />
                      ) : (
                        <ChevronRight className="w-4 h-4" />
                      )}
                    </button>
                    
                    {isExpanded && (
                      <div className="ml-6 space-y-1">
                        {item.children.map((child) => {
                          const ChildIcon = child.icon;
                          return (
                            <button
                              key={child.id}
                              onClick={() => {
                                setActiveTab(child.id);
                                setIsOpen(false);
                              }}
                              className={`
                                w-full flex items-center px-4 py-2 text-left rounded-lg transition-colors text-sm
                                ${activeTab === child.id 
                                  ? 'bg-blue-50 text-blue-700 border-r-2 border-blue-700' 
                                  : 'text-gray-600 hover:bg-gray-100'
                                }
                              `}
                            >
                              <ChildIcon className="w-4 h-4 mr-3" />
                              <span>{child.label}</span>
                            </button>
                          );
                        })}
                      </div>
                    )}
                  </div>
                );
              }
              
              return (
                <button
                  key={item.id}
                  onClick={() => {
                    setActiveTab(item.id);
                    setIsOpen(false);
                  }}
                  className={`
                    w-full flex items-center px-4 py-3 text-left rounded-lg transition-colors
                    ${activeTab === item.id 
                      ? 'bg-blue-50 text-blue-700 border-r-2 border-blue-700' 
                      : 'text-gray-700 hover:bg-gray-100'
                    }
                  `}
                >
                  <Icon className="w-5 h-5 mr-3" />
                  <span className="font-medium">{item.label}</span>
                </button>
              );
            })}
          </nav>
        </div>
      </div>
    </>
  );
};

export default Sidebar;
