import React, { useState } from 'react';
import Dashboard from './pages/Dashboard';
import Policies from './pages/Policies';
import API from './pages/API';
import Products from './pages/Products';
import Categories from './pages/Categories';
import Orders from './pages/Orders';
import BasicInformation from './pages/BasicInformation';
import Site from './pages/Site';
import Footer from './pages/Footer';
import Customers from './pages/Customers';
import UGCVideos from './pages/UGCVideos';
import OfferManagement from './pages/OfferManagement';
import ReviewManagement from './pages/ReviewManagement';
import BlogPosts from './pages/BlogPosts';
import BlogCategories from './pages/BlogCategories';
import BlogFAQs from './pages/BlogFAQs';
import BlogSEO from './pages/BlogSEO';
import Sidebar from './Sidebar';
import { Menu, LogOut, Users } from 'lucide-react';

const AdminPanel = ({ authToken, onLogout }) => {
  const [activeTab, setActiveTab] = useState('dashboard');
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const renderContent = () => {
    switch(activeTab) {
      case 'dashboard':
        return <Dashboard authToken={authToken} setActiveTab={setActiveTab} />;
      case 'basic-info':
        return <BasicInformation authToken={authToken} />;
      case 'site':
        return <Site authToken={authToken} />;
      case 'footer':
        return <Footer authToken={authToken} />;
      case 'policies':
        return <Policies authToken={authToken} />;
      case 'api':
        return <API authToken={authToken} />;
      case 'products':
        return <Products authToken={authToken} />;
      case 'categories':
        return <Categories authToken={authToken} />;
      case 'orders':
        return <Orders authToken={authToken} />;
      case 'customers':
        return <Customers authToken={authToken} />;
      case 'ugc-videos':
        return <UGCVideos authToken={authToken} />;
      case 'offer-management':
        return <OfferManagement authToken={authToken} />;
      case 'review-management':
        return <ReviewManagement authToken={authToken} />;
      case 'blog-posts':
        return <BlogPosts authToken={authToken} />;
      case 'blog-categories':
        return <BlogCategories authToken={authToken} />;
      case 'blog-faqs':
        return <BlogFAQs authToken={authToken} />;
      case 'blog-seo':
        return <BlogSEO authToken={authToken} />;
      default:
        return <Dashboard authToken={authToken} />;
    }
  };

  return (
    <div className="h-screen bg-gray-50 flex overflow-hidden">
      {/* Sidebar */}
      <Sidebar 
        activeTab={activeTab} 
        setActiveTab={setActiveTab} 
        isOpen={sidebarOpen} 
        setIsOpen={setSidebarOpen} 
      />
      
      {/* Main Content Area */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Top Header */}
        <header className="bg-white shadow-sm border-b border-gray-200">
          <div className="flex items-center justify-between h-16 px-6">
            <div className="flex items-center space-x-4">
              {/* Mobile Menu Button */}
              <button
                onClick={() => setSidebarOpen(true)}
                className="lg:hidden p-2 rounded-md hover:bg-gray-100"
              >
                <Menu className="w-5 h-5" />
              </button>
              
              <h1 className="text-xl font-bold text-gray-900">Admin Panel</h1>
            </div>
            
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-3">
                <div className="w-8 h-8 bg-blue-500 rounded-full flex items-center justify-center">
                  <Users className="h-4 w-4 text-white" />
                </div>
                <button
                  onClick={() => {
                    if (window.confirm('Are you sure you want to logout?')) {
                      onLogout();
                    }
                  }}
                  className="flex items-center space-x-2 px-3 py-2 text-sm text-gray-700 hover:bg-gray-100 rounded-md transition-colors"
                  title="Logout"
                >
                  <LogOut className="h-4 w-4" />
                  <span className="hidden sm:block">Logout</span>
                </button>
              </div>
            </div>
          </div>
        </header>
        
        {/* Main Content */}
        <main className="flex-1 p-6 overflow-y-auto">
          {renderContent()}
        </main>
      </div>
    </div>
  );
};

export default AdminPanel;